<template>
  <view class="page">
    <!-- È°∂ÈÉ®Ê†è -->
    <view class="header">
      <text class="title">ËèúË∞±Â§ßÂÖ®</text>
      <text class="icon-search" @click="onSearch">üîç</text>
    </view>

    <!-- ÂàÜÁ±ªÁ≠õÈÄâ -->
    <scroll-view class="chips" scroll-x>
      <view
        v-for="c in cats"
        :key="c.key"
        :class="['chip', activeCat === c.key ? 'active' : '']"
        @click="activeCat = c.key"
      >
        {{ c.name }}
      </view>
    </scroll-view>

    <!-- ‰∏§ÂàóÂç°ÁâáÁΩëÊ†º -->
    <view class="grid">
      <view class="card" v-for="r in filteredList" :key="r.id" @click="openRecipe(r)">
        <image class="cover" :src="r.cover" mode="aspectFill" />
        <view class="info">
          <text class="name">{{ r.name }}</text>
          <view class="meta">
            <view class="tag" :class="r.level === 'ÁÆÄÂçï' ? 'tag-green' : 'tag-orange'">{{ r.level }}</view>
            <text class="time">{{ r.time }}min</text>
          </view>
        </view>
      </view>
    </view>

    <view style="height: 24rpx;" />
  </view>
</template>

<script>
export default {
  onLoad(options) {
    if (options && options.cat) {
      this.activeCat = options.cat;
    }
  },
  onShow() {
    const cat = uni.getStorageSync('recipes_cat');
    if (cat) {
      this.activeCat = cat;
      uni.removeStorageSync('recipes_cat');
    }
  },
  data() {
    return {
      activeCat: 'all',
      cats: [
        { key: 'all', name: 'ÂÖ®ÈÉ®' },
        { key: 'cn', name: '‰∏≠Âºè' },
        { key: 'west', name: 'Ë•øÂºè' },
        { key: 'jp', name: 'Êó•Âºè' },
        { key: 'kr', name: 'Èü©Âºè' }
      ],
      // Á§∫‰æãÊï∞ÊçÆÔºöÂèØÊõøÊç¢‰∏∫Êé•Âè£ËøîÂõû
      list: [
        { id: 'mapo-tofu', name: 'È∫ªÂ©ÜË±ÜËÖê', level: 'ÁÆÄÂçï', time: 20, cat: 'cn', cover: '/static/logo.png' },
        { id: 'egg-fried-rice', name: 'ËõãÁÇíÈ•≠', level: 'ÁÆÄÂçï', time: 15, cat: 'cn', cover: '/static/logo.png' },
        { id: 'hongshao-rou', name: 'Á∫¢ÁÉßËÇâ', level: '‰∏≠Á≠â', time: 45, cat: 'cn', cover: '/static/logo.png' },
        { id: 'tangcu-liji', name: 'Á≥ñÈÜãÈáåËÑä', level: '‰∏≠Á≠â', time: 35, cat: 'cn', cover: '/static/logo.png' },
        { id: 'sushi', name: 'ÂØøÂè∏ÊãºÁõò', level: '‰∏≠Á≠â', time: 40, cat: 'jp', cover: '/static/logo.png' },
        { id: 'bibimbap', name: 'Áü≥ÈîÖÊãåÈ•≠', level: 'ÁÆÄÂçï', time: 25, cat: 'kr', cover: '/static/logo.png' }
      ]
    }
  },
  computed: {
    filteredList() {
      if (this.activeCat === 'all') return this.list
      return this.list.filter(i => i.cat === this.activeCat)
    }
  },
  methods: {
    onSearch() {
      uni.showToast({ title: 'ÊêúÁ¥¢ÊöÇÊú™Êé•ÂÖ•', icon: 'none' })
    },
    openRecipe(r) {
      uni.showToast({ title: 'ÊâìÂºÄÔºö' + r.name, icon: 'none' })
      // ÂèØË∑≥ËΩ¨Âà∞ËØ¶ÊÉÖÈ°µ
      // uni.navigateTo({ url: '/pages/recipe/detail?id=' + r.id })
    }
  }
}
</script>

<style>
.page {
  padding: 24rpx;
  background: #ffffff;
  min-height: 100vh;
  box-sizing: border-box;
}

/* È°∂ÈÉ®Ê†è */
.header {
  height: 88rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title {
  font-size: 36rpx;
  font-weight: 700;
  color: #1a1f36;
}
.icon-search {
  font-size: 36rpx;
  color: #5f6368;
}

/* ÂàÜÁ±ª Chip */
.chips {
  margin-top: 16rpx;
  white-space: nowrap;
}
.chip {
  display: inline-flex;
  padding: 12rpx 24rpx;
  background: #f3f4f6;
  color: #374151;
  border-radius: 999rpx;
  font-size: 26rpx;
  margin-right: 16rpx;
}
.chip.active {
  color: #fff;
  background: linear-gradient(90deg, #ff8a34 0%, #ff6a00 100%);
  box-shadow: 0 6rpx 16rpx rgba(255, 122, 0, 0.3);
}

/* ÁΩëÊ†º */
.grid {
  margin-top: 20rpx;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* Âç°Áâá */
.card {
  width: calc(50% - 10rpx);
  background: #fff;
  border-radius: 24rpx;
  overflow: hidden;
  margin-bottom: 20rpx;
  box-shadow: 0 10rpx 30rpx rgba(0,0,0,0.06);
}
.cover {
  width: 100%;
  height: 300rpx;
  background: #eee;
}
.info {
  padding: 18rpx;
}
.name {
  font-size: 28rpx;
  color: #1f2937;
}
.meta {
  margin-top: 14rpx;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.tag {
  padding: 6rpx 16rpx;
  border-radius: 999rpx;
  font-size: 22rpx;
}
.tag-green {
  background: #e9f8ee;
  color: #27ae60;
}
.tag-orange {
  background: #fff3e6;
  color: #ff8a34;
}
.time {
  font-size: 24rpx;
  color: #6b7280;
}
</style>